/// solution

function filter(array, fn) {
  if (array.length === 0) {
    return []
  }
  let head = []
  if (fn(array[0])) {
    head = [array[0]]
  }
  return head.concat(filter(array.slice(1), fn))
}

/// tests

import { test } from 'ava'

test(t => t.deepEqual(
  filter([1, 2, 3, 4], function(n) { return n > 0 }),
  [1, 2, 3, 4]
))
test(t => t.deepEqual(
  filter([1, 2, 3, 4], function(n) { return n < 3 }),
  [1, 2]
))
